<template>
    <div class="navbar navbar-default container">
        <div class="container-fluid">
            <div class="navbar-header">
                <a class="navbar-brand" href="#">Awesome</a>
            </div>
            <div class="collapse navbar-collapse" id="bs-navbar-collapse-1">
                <ul class="nav navbar-nav">
                    <router-link :to="'/profile'" tag="li" active-class="active"><a>Profile</a></router-link>
                    <router-link :to="'/register'" tag="li" active-class="active" v-if="!getUserInfo.authorized"><a>Register</a></router-link>
                    <router-link :to="'/login'" tag="li" active-class="active"  v-if="!getUserInfo.authorized"><a>Login</a></router-link>
                    <li v-if="getUserInfo.authorized" @click="logout"><a>Logout</a></li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapActions, mapGetters} from 'vuex'
    export default{
        data(){
            return {
            }
        },
        methods: {
            ...mapActions([
                'logout'
            ])
        },
        computed: {
            ...mapGetters([
                'getUserInfo'
            ])
        }
    }
</script>
<style>

</style>



