<template>
    <div class="container">
        <div class="row" v-if="!getUserInfo.authorized">
            <div class="alertv alert-danger">
                <p>For editing profile you should be authorized</p>
            </div>
        </div>
        <div class="row" v-if="getUserInfo.authorized">
            <div class="col-xs-12 col-sm-6 col-md-6">
                <div class="well well-sm">
                    <div class="row">
                        <div class="col-sm-6 col-md-4">
                            <img src="http://placehold.it/380x500" alt="" class="img-rounded img-responsive" v-if="!getUserInfo.user_avatar_name">
                            <img :src="'./avatar/' + getUserInfo.user_avatar_name"  alt="#" class="img-rounded img-responsive" v-if="getUserInfo.user_avatar_name">
                        </div>
                        <div class="col-sm-6 col-md-8">
                            <h3>Имя: {{getUserInfo.user_name}}</h3>
                            <h4>Возраст: {{getUserInfo.user_age}}</h4>
                            <p v-if="getUserInfo.is_locked">К сожелению вы не можете редактировать свой профиль. Вы заблокированы. Обратитесь к администратору.</p>
                            <div class="btn-group">
                                <button type="button" class="btn btn-primary"  v-if="!getUserInfo.is_locked" @click="isShownFormProfile = !isShownFormProfile">Редактровать</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="row" v-if="isShownFormProfile">
            <div class="col-lg-4"></div>
            <div class="col-lg-4">
                <div class="form-group">
                    <label for="input-name">Name</label>
                    <input type="email" class="form-control" id="input-name" v-model="user.user_name">
                </div>
                <div class="form-group">
                    <label for="input-age">Age</label>
                    <input type="text" class="form-control" id="input-age" v-model="user.user_age">
                </div>
                <div class="form-group">
                    <label for="product-image">Изображение</label>
                    <input type="file" id="product-image" @change="upload($event)">
                </div>
                <button type="button" class="btn btn-default" @click="editUserProfile">Edit</button>
            </div>
            <div class="col-lg-4"></div>
        </div>
        <div class="row" v-if="getUserInfo.superuser && getUserList.length > 0">
            <table class="table table-striped">
                <thead>
                    <tr>
                        <th>#</th>
                        <th>User Name</th>
                        <th>User Age</th>
                        <th>Avatar</th>
                        <th>Lock</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(user, i) in getUserList">
                        <td scope="row">{{i+1}}</td>
                        <td>{{user.user_name}}</td>
                        <td>{{user.user_age ? user.user_age : 'not set'}}</td>
                        <td>{{user.user_avatar_name}}</td>
                        <td>
                            <button type="button" class="btn btn-default" @click="lockUserProfile({id: user.id, lock: true})" v-if="!user.is_locked">Заблокировать</button>
                            <button type="button" class="btn btn-default" @click="lockUserProfile({id: user.id, lock: false})" v-if="user.is_locked">Разблокировать</button>
                        </td>
                    </tr>
                </tbody>
            </table>
            <!--<figure class="highlight">-->
                <!--<button type="button" class="btn btn-default" @click="lockUserProfile">Update</button>-->
            <!--</figure>-->
        </div>
    </div>
</template>


<script>
    import {mapActions, mapGetters} from 'vuex'
  export default{
    data(){
      return {
          user: {
              user_name: null,
              user_age: null
          },
          formData: new FormData(),
          isShownFormProfile: false,
          checkedUserForLock: []
      }
    },
    methods: {
      ...mapActions([
          'getUserInformation',
          'editUserProfileAction',
          'lockUserProfileAction'
      ]),
        upload(e){
            this.formData.set('images', e.target.files[0])
        },
        editUserProfile(){
            let data = {
                method: 'editProfile',
                data: this.user
            }
            let dataJSON = JSON.stringify(data);
            this.formData.set('data', dataJSON);
            this.editUserProfileAction(this.formData)
        },
        lockUserProfile(userInfo){
            let data = {
                method: 'lockUsers',
                data: userInfo
            }
            let dataJSON = JSON.stringify(data);
            let formData = new FormData();
            formData.set('data', dataJSON);
            this.lockUserProfileAction(formData)
        }
    },
      computed: {
      ...mapGetters([
          'getUserInfo',
            'getUserList',
          'showPreloader'
          ])
      },
      created(){
        this.getUserInformation();

      }
  }
</script>



