<template>
  <div class="container">
    <div class="row">
      <div class="col-lg-4"></div>
      <div class="col-lg-4">
          <div class="form-group">
            <label for="register-username">Username</label>
            <input type="text" class="form-control" id="register-username" placeholder="Paul" v-model="user.username">
          </div>
          <div class="form-group">
            <label for="register-password">Password</label>
            <input type="password" class="form-control" id="register-password" placeholder="Password" v-model="user.password">
          </div>
          <button type="button" class="btn btn-default" @click="register(user)">Submit</button>
      </div>
      <div class="col-lg-4"></div>
    </div>
  </div>
</template>


<script>
    import {mapActions} from 'vuex'
  export default{
    data(){
      return {
        user: {
          username: null,
          password: null
        }
      }
    },
    methods: {
      ...mapActions([
        'register'
      ])
    }
  }
</script>



