export const setInformationMsg = (state, payload) => {
    state.msg.text = payload.text;
    for(let className in state.msg.className){
        state.msg.className[className] = false;
    }
    if(payload.className){
        state.msg.className[payload.className] = true;
    }
}

export const logInOut = (state, payload) => {
    if(!payload){
        state.user = {
          authorized: false,
          superuser: false
        };
        state.userList = null;
        localStorage.removeItem('access_token');
        localStorage.removeItem('id_token');
    }
    state.user.authorized = payload
}

export const setUserInformation = (state, payload) => {
    let user = state.user;
    for(let key in payload.cur_user){
      user[key] = payload.cur_user[key];
    }
    for(let key in user){
        if(user[key] === "TRUE" && user.hasOwnProperty(key)){
            user[key] = true;
        } else if (user[key] === "FALSE" && user.hasOwnProperty(key)){
            user[key] = false;
        }
    }
    state.userList = payload.group;
    for(let user of state.userList){
        for(let key in user){
            if(user[key] === "TRUE" && user.hasOwnProperty(key)){
                user[key] = true;
            } else if (user[key] === "FALSE" && user.hasOwnProperty(key)){
                user[key] = false;
            }
        }
    }
    if(payload.cur_user.user_role === 'admin') user.superuser = true;
}
export const changePreloaderStatus = (state, payload) => {
    state.showPreloader = !state.showPreloader;

}



