<template>
  <div class="container">
    <div class="row">
      <div class="col-lg-4"></div>
      <div class="col-lg-4">
          <div class="form-group">
            <label for="input-login">Login</label>
            <input type="email" class="form-control" id="input-login" placeholder="login" v-model="user.login">
          </div>
          <div class="form-group">
            <label for="input-password">Password</label>
            <input type="password" class="form-control" id="input-password" placeholder="Password" v-model="user.password">
          </div>
          <button type="submit" class="btn btn-default" @click="login(user)">Submit</button>
      </div>
      <div class="col-lg-4"></div>
    </div>
  </div>
</template>


<script>
  import {mapActions} from 'vuex'
  export default{
    data(){
      return {
        user: {
          login: null,
          password: null
        }
      }
    },
    methods: {
      ...mapActions([
        'login'
      ])
    }
  }
</script>



