import Vue from 'vue'
import router from '../router'

const api = {
  serverURL: 'http://localhost:3302/api/'
}

function requestToServer(urlEnd, type, payload = {}){
    if(type == 'get'){
        return Vue.http[type](api.serverURL + urlEnd, {headers: {'Authorization': 'Bearer ' + localStorage['access_token']}})
    }else{
        return Vue.http[type](api.serverURL + urlEnd, payload, {headers: {'Authorization': 'Bearer ' + localStorage['access_token']}})
    }
}

export const register = ({ commit }, payload) => {
    requestToServer('registration', 'post', payload).then(
        resolve => {
            console.log('resolve', resolve)
            setInformationMsg({commit}, {'text': resolve.body, 'className' : 'alert-success'});
            router.push('/login')
        }
  ).catch((err) => {
        console.log('err', err)
        setInformationMsg({commit}, {'text': err.body, 'className' : 'alert-danger'});
  })
}

export const setInformationMsg = ({commit}, payload) => {
  commit('setInformationMsg', {'text': payload.text, 'className': payload.className});
  setTimeout(() => {
    commit('setInformationMsg', {'text': '', 'className': ''})
  }, 5000)
}

export const login = ({ commit }, payload) => {
  requestToServer('login', 'post', payload).then(
    resolve => {
      console.log('resolve', resolve)
      localStorage.setItem('id_token', resolve.body.id_token);
      localStorage.setItem('access_token', resolve.body.access_token);
        commit('logInOut', true)
        setInformationMsg({commit}, {'text': 'Congratulations!', 'className' : 'alert-success'});
        getUserInformation({ commit })
        router.push('/profile');
    }
  ).catch((err) => {
      console.log('err', err)
      setInformationMsg({commit}, {'text': err.body, 'className' : 'alert-danger'});
  })
}

export const logout = ({ commit }, payload) => {
    commit('logInOut', false);
    router.push('/login');
}

export const getUserInformation = ({ commit }, payload) => {
    requestToServer('profile', 'get', payload).then(
        resolve => {
            console.log('resolve', resolve)
            commit('setUserInformation', resolve.body)
            commit('logInOut', true)
        }
    ).catch((err) => {
        console.log('err', err)
        setInformationMsg({commit}, {'text': err.body, 'className' : 'alert-danger'});
    })
}

export const editUserProfileAction = ({ commit }, payload) => {
    commit('changePreloaderStatus');
    requestToServer('profile', 'put', payload).then(
        resolve => {
            console.log('resolve', resolve)
            setInformationMsg({commit}, {'text': resolve.body, 'className' : 'alert-success'});
            getUserInformation({commit});
            commit('changePreloaderStatus');
        }
    ).catch((err) => {
        console.log('err', err)
        setInformationMsg({commit}, {'text': err.body, 'className' : 'alert-danger'});
    })
}

export const lockUserProfileAction = ({ commit }, payload) => {
    commit('changePreloaderStatus');
    requestToServer('profile', 'put', payload).then(
        resolve => {
            console.log('resolve', resolve)
            setInformationMsg({commit}, {'text': resolve.body, 'className' : 'alert-success'});
            getUserInformation({commit});
            commit('changePreloaderStatus');
        }
    ).catch((err) => {
        console.log('err', err)
        setInformationMsg({commit}, {'text': err.body, 'className' : 'alert-danger'});
    })
}



