import Vue from  'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import * as getters from  './getters'
import * as mutations from './mutations'
import * as actions from './actions'
export const store = new Vuex.Store({
    state: {
        user: {
            authorized: false,
            superuser: false
        },
        userList: null,
        msg: {
            text: '',
            className: {'alert-success': false, 'alert-danger': false}
        },
        showPreloader: false
    },
    getters,
    mutations,
    actions
})





